.navBar {
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
}

.appHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  height: 40px;
  padding: 0 200px;
  background-color: #ffffff;
  border-bottom: 1px solid #eaeaeb;
}

@media screen and (max-width: 768px) {
  .App-header {
    padding: 0 10px;
  }
}



